<template>
    <div class="header" v-if="visible">
        <div class="image-container">
            <!-- 左 鄂电红马甲图标 -->
            <img class="img_left" :src="logo" alt="" title="鄂电红马甲" />

            <img :src="slogan" alt="" title="荆门东宝便民服务地图" class="img_center" />

            <div class="circle_btn" @click="showList = !showList">
                <img src="../assets/icon-selection.png" class="img_right" />
            </div>


            <div v-if="showList" class="pop-nav pop-box">
                <div v-if="isWeChat" class="nav-item"
                    @click="navigateToMiniProgram('wx943766bf30b9b378', 'gh_92454cf4881d', '/pages/payment/payment.html')">
                    <wx-open-launch-weapp 
                        id="launch-btn" 
                        appId="wx5899bdb8721621d6" 
                        username="gh_56b2c43416a4"
                        path="/pages/index/index.html">

                        <script type="text/wxtag-template">
                        <style scoped>
                            * {
                                -webkit-text-size-adjust: none;
                                -webkit-tap-highlight-color: rgba(0,0,0,0);
                                -webkit-user-select: none;
                                user-select: none;
                            }
                            .btn {								
                                display: flex;
                                align-items: center;
                                font-size: 12px;
                                line-height: 1;
                                color: #666 !important;
                            }
                            .btn img {
                                margin-right: 6px;
                                width: 16px;
                                height: 16px;
                            }
                        </style>
                            <a class="btn">
                                <img src="https://map.hongleme.cn/static/images/icon-jiaofei.png">去交费
                            </a>                                                                                     
                   </script>
                    </wx-open-launch-weapp>
                </div>
                <div v-if="isWeChat" class="nav-item"
                    @click="navigateToMiniProgram('wx943766bf30b9b378', 'gh_92454cf4881d', '/pages/charging/charging.html')">
                    <wx-open-launch-weapp 
                        id="launch-btn" 
                        appid="wx943766bf30b9b378" 
                        username="gh_92454cf4881d"
                        path="/pages/lookForCharging/stationMap/stationMap.html">

                        <script type="text/wxtag-template">
                        <style scoped>
                            .btn {								
                      		    display: flex;
                                align-items: center;
                                font-size: 12px;
                                line-height: 1;
                                color: #666 !important;
                           	}
                            .btn img {
                           		margin-right: 6px;
                            	width: 16px;
                           		height: 16px;
                            }
                        </style>
                        <a class="btn">
                            <img src="https://map.hongleme.cn/static/images/icon-e.png">e充电
                        </a>
                        </script>
                    </wx-open-launch-weapp>

                </div>
                <div class="nav-item" v-for="(btn, index) in button_list" @click="showList = !showList">
                    <router-link :key="index" :to="routerTo(index)">
                        <img :src="btn.imgSrc">{{ btn.label }}
                    </router-link>
                </div>
                <div class="nav-item">
                    <a href="https://csc-static.sgcc.com.cn:28888/app/download/index.html">
                        <img src="../assets/icon-logo.png">网上国网APP
                    </a>
                </div>
            </div>
        </div>
        <div class="button-container">
            <router-link v-for="(btn, index) in buttons" :key="index" :to="routerTo(index)" class="button">{{ btn.label
            }}</router-link>
        </div>
    </div>
</template>

<script>
import router from '../router';
import { eventBus } from '../main';
export default {
    name: "Header",
    data() {
        return {
            showList: false, // 是否显示列表
            isWeChat: false, // 新增的变量，用于判断是否在微信端
            visible: true,
            logo: require("../assets/logo.f5ffba99.png"),
            slogan: require("../assets/slogan.abb42670.png"),
            buttons: [
                { label: "电价政策", href: require("../assets/zhengche.jpg") },
                { label: "报装政策", href: require("../assets/baozhuang1.jpg") },
                { label: "综合能源服务", href: require("../assets/zonghe.jpg") }
            ],
            button_list: [
                { label: "电价政策", href: require("../assets/zhengche.jpg"), imgSrc: require("../assets/icon-baozhuang.png") },
                { label: "报装政策", href: require("../assets/baozhuang1.jpg"), imgSrc: require("../assets/icon-zhengche.png") },
                { label: "综合能源服务", href: require("../assets/zonghe.jpg"), imgSrc: require("../assets/icon-zonghe.png") }
            ],
        };
    },
    mounted() {
        this.checkWeChat();
        this.getWxLaunch();
        eventBus.$on('hideHome', () => {
            this.hide()
        })
        eventBus.$on('showHome', () => {
            this.show()
        })
    },
    methods: {
        async getWxLaunch() {
            const params = {
                url: window.location.href.split('#')[0]
            };
            await this.$http({
                method: 'post',
                url: 'https://predigitalconstruction.mfxapp.com/mfx/event/getWxSign',
                data: params
            }).then(({ data }) => {
                console.log('wx', wx);
                console.log('data', data);
                wx.config({
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    appId: 'wx5811b6971f7d6d60', // 必填，企业号的唯一标识，此处填写企业号corpid
                    timestamp: data.data.timestamp, // 必填，生成签名的时间戳
                    nonceStr: data.data.nonceStr, // 必填，生成签名的随机串
                    signature: data.data.signature, // 必填，签名
                    jsApiList: ['wx-open-subscribe', 'wx-open-launch-weapp'], // 必填，需要使用的JS接口列表
                    openTagList: ['wx-open-launch-weapp']
                });
                wx.ready(() => {
                    console.log('成功1111111');
                });
                wx.error(function (res1) {
                    console.log('出错', res1, res1.errMsg);
                });
            });

        },
        checkWeChat() {
            const ua = window.navigator.userAgent.toLowerCase();
            if (ua.match(/MicroMessenger/i) && ua.match(/MicroMessenger/i)[0] === "micromessenger") {
                this.isWeChat = true;
            } else {
                this.isWeChat = false;
            }
        },
        initWechatSDK() {
            const appId = '你的微信公众号 AppID';
            const nonceStr = '自动生成的随机字符串，最好每次都不同';
            const timestamp = Math.floor(Date.now() / 1000);
            const url = window.location.href;

            // 发起 AJAX 请求获取微信配置参数
            fetch(`/wechat/signature?url=${encodeURIComponent(url)}`).then(response => response.json()).then(data => {
                if (data.code === 0) {
                    wx.config({
                        debug: false,
                        appId: appId,
                        nonceStr: nonceStr,
                        timestamp: timestamp,
                        signature: data.data.signature,
                        jsApiList: ['navigateToMiniProgram']
                    });
                } else {
                    console.error('初始化微信 JS-SDK 失败', data.message);
                }
            }).catch(error => {
                console.error('初始化微信 JS-SDK 失败', error);
            });
        },

        navigateToMiniProgram(appId, orginalId, path) {
            if (this.isWeChat) {
                wx.navigateToMiniProgram({
                    appId: appId,
                    path: path,
                    extraData: {},
                    envVersion: 'trial',
                    success(res) {
                        console.log('跳转到小程序成功:', res);
                    },
                    fail(err) {
                        console.error('跳转到小程序失败:', err);
                    }
                });
            }
        },
        navigateToMiniProgram(appid, username, path) {
            if (this.isWeChat) {
                wx.miniProgram.navigateTo({
                    appId: appid,
                    userName: username,
                    path: path,
                    success() {
                        console.log("跳转成功");
                    },
                    fail(err) {
                        console.log("跳转失败", err);
                    }
                });
            }
        },
        hide() {
            this.visible = false
        },
        show() {
            this.visible = true
        },
        routerTo(index) {
            return "/OpenPict?id=" + index;
        },
    }
};
</script>

<style type="text/css">
body,
html,
#container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    margin: 0;
    font-family: "微软雅黑";
}

/* 清除默认边距 */
* {
    margin: 0;
    padding: 0;
}

html {
    height: 100%;
}

a {
    text-decoration: none;
    color: #333;
}

.header {
    top: 0;
    left: 0;
    width: 100%;
    height: 125px;
    background: rgba(230, 242, 221, 0.95);
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    position: relative;
    justify-content: center;
}

.image-container {
    position: relative;
    display: flex;
    width: 100%;
    height: 48px;
    flex-direction: row;
    align-items: center;
    top: -26px;
    /* 添加一些间距 */
}

.image-class {
    position: fixed;
    right: 0;
    bottom: 20px;
    width: 180px;
    /*设置图片的宽度*/
    height: 164px;
    /*设置图片的高度*/
}

.button-container {
    width: 97%;
    height: 35px;
    padding: 0 10px;
    display: flex;
    align-items: center;
    background: hsla(0, 0%, 100%, 0.7);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.04);
    backdrop-filter: blur(17px px);
    border-radius: 5px;
    overflow-x: auto;
    flex-direction: row;
    /* 让按钮并排显示 */
    justify-content: space-between;
    /* 在容器内均匀分布按钮 */
    position: absolute;
    /* 使按钮容器相对于 .header 定位 */
    bottom: 10px;
    /* 将小div的底部对齐设置为0 */
}

.button {
    display: flex;
    flex-grow: 1;
    padding: 0 8px;
    margin: 0px 3px;
    height: 22px;
    font-size: 12px;
    color: #000;
    line-height: 22px;
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    background: #fff;
    border: 1px solid #5bd162;
    box-shadow: 0 2px 5px #f5f5f5;
    border-radius: 24px;
    justify-content: center;
}

.img_center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 216px;
    height: 51px;
}

.img_left {
    position: absolute;
    top: 5px;
    left: 16px;
    width: 36px;
}

.circle_btn {
    cursor: pointer;
    position: absolute;
    width: 48px;
    height: 48px;
    right: 15px;
    border-radius: 50%;
    /* 将外部div的边框半径设置为50%以创建圆形 */
    box-shadow: 0 4px 4px rgba(37, 136, 57, 0.2);
    justify-content: center;
    /* 水平居中 */
    align-items: center;
    /* 垂直居中 */
    overflow: hidden;
    /* 隐藏圆形外部div中超出的部分 */
    display: flex;
    background: #fff;
    transition: all 0.3s;
}

/* 鼠标边框变色 */
.circle_btn:hover {
    box-shadow: 0 4px 4px rgba(37, 136, 57, 0.3);
}

.search-list {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    padding: 12px 16px;
}

.circle_btn:hover .search-list {
    margin-top: 100px;
    display: block;
}

.img_right {
    position: absolute;
    width: 40%;
    height: 40%;
}



.btn {
    width: 60px;
    height: 30px;
    float: left;
    background-color: #fff;
    color: rgba(27, 142, 236, 1);
    font-size: 14px;
    border: 1px solid rgba(27, 142, 236, 1);
    border-radius: 5px;
    margin: 5px 6px;
    text-align: center;
    line-height: 30px;
}

.btn:hover {
    background-color: rgba(27, 142, 236, 0.8);
    color: #fff;
    cursor: pointer;
}

ul li {
    list-style: none;
}

.nav {
    top: 0;
    left: 0;
    width: 100%;
    height: 125px;
    background: rgba(230, 242, 221, .95)
}

.pop-box {
    position: fixed;
    z-index: 9999999
}

.pop-nav {
    top: 76px;
    right: 16px;
    padding: 16px 12px;
    width: 120px;
    height: auto;
    background: #fff;
    box-shadow: 0 4px 4px rgba(37, 136, 57, .17);
    border-radius: 4px;
    z-index: 9999999
}

.pop-nav::before {
    position: absolute;
    top: -20px;
    right: 12px;
    width: 0;
    height: 0;
    display: block;
    content: "";
    border: 10px solid transparent;
    border-bottom-color: #fff
}


.nav-item {
    display: block;
    border: flex
}

.nav-item a,
.nav-item uni-navigator {
    display: flex;
    align-items: center;
    font-size: 12px;
    line-height: 1
}

.nav-item a:visited,
.nav-item a:-webkit-any-link,
.nav-item a:active,
.nav-item uni-navigator:visited,
.nav-item uni-navigator:-webkit-any-link,
.nav-item uni-navigator:active {
    color: #666;
    text-decoration: none
}

.nav-item img {
    margin-right: 6px;
    width: 16px;
    height: 16px
}

.nav-item+.nav-item {
    margin-top: 20px
}

@media screen and (max-width: 1000px) {
    .header {
        top: 0;
        left: 0;
        width: 100%;
        height: 125px;
        background: rgba(230, 242, 221, 0.95);
        display: flex;
        flex-direction: row;
        align-items: center;
        /* text-align: center; */
        text-align: left !important;
        position: relative;
        justify-content: center;
    }
}

@media screen and (max-width: 1000px) {
    .img_left {
        position: absolute;
        top: 5px;
        left: 16px;
        width: 36px;
        height: 36px;
    }
}
</style>
