<template>
  <div id="app">
    <!-- <nav>
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </nav>
    <router-view/> -->
    <RouterView ></RouterView>
    <HeaderBtn></HeaderBtn>
    <MapView :m="giveData"></MapView>
  </div>
</template>
<script>
import MapView from './components/Map.vue'
import HeaderBtn from './components/HeaderBtn.vue'
import pcOrMobile from '../src/data/pcOrMobile'
import { RouterView } from 'vue-router';

export default {
  mixins:[pcOrMobile],
  data () {
    return {
      giveData:{  //vue2.0将数据定位对象的形式，实现父子组件的通信
        height:0,
        longitude:112.20518,
        latitude:31.022003
      },
    }
  },
  components:{
   
    MapView,
    HeaderBtn, 
    RouterView,
  },
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
    this.isPcOrMobile();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    handleResize() {
      this.giveData.height = window.innerHeight;
    },
    isPcOrMobile() {
      var title = '我是标题'
      var message = '我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容我是内容'
      //安卓手机-iOS-pc判断开始
      if (this.isMobile_pc()) {
        if (
          this.is_andriod_ios().iPhone ||
          this.is_andriod_ios().iPad ||
          this.is_andriod_ios().ios
        ) {
          // ios内容
          console.log( window.webkit,'ios');
          // 下面是传递值到苹果手机 showPopupIos 该调用方法名与苹果工程师沟通一至
          window.webkit.messageHandlers.showPopupIos.postMessage({
            name: title,
            content: message
          });
        }
        if (this.is_andriod_ios().android) {
          // 安卓内容
          // alert('android');
          // 下面是传递值到安卓手机，showPopupAndroid 该调用方法名与安卓工程师沟通一至
          AndroidJs.showPopupAndroid(title, message)
        }
      } else {
        // pc端内容
        // console.log('pc');
        // alert('我是pc弹出')
        localStorage.setItem('myDeviceType', 'pc');
      }
      // 安卓手机-iOS-pc判断结束
      },
  },
}
</script>
