                <div class="msg-navigation" @click="chooseMap(item.title)"><img src="../assets/icon-arrow.png">到这去</div>
                <div class="msg-intro">{{ item.intro }}</div>
                <div class="msg-info" v-for="hotline in item.label" :key="hotline.name">
                    <div class="msg-info-title">{{ hotline.name }}</div>
                    <div class="msg-info-list" v-for="li in hotline.list" :key="li.tag">
                        <div>{{ li.tag }} 
                            <span>{{ li.man }} </span>
                            <a class="tel-link" :href="getTelLink(li.tel)" data-tel="li.tel">{{ li.tel }}</a>
