  <script>
  import { eventBus } from '../main';
  import drawerContent from '../data/data';
  export default {
    data() {
      return {
        showText: false,
        showDrawer: false, // 是否显示抽屉
        showchooseMap: false,//是否显示地图选择
        drawerContent: drawerContent,
        selectedCommunity: null,
        Communityshow: null,
        lng: null,//记录当前点击社区的经纬度
        lat:null,
        //myPositon: null,
        cur_lng: null,
        cur_lat: null,//当前位置
      };
    },
    mounted() {
        eventBus.$on('map-click', () => {
            this.showText = false;
            this.showDrawer = false;
            this.showchooseMap = false;
        });      
        eventBus.$on('my_position', (lng,lat) => {   
            //this.myPositon = lat+","+lng;
            this.cur_lat = lat;
            this.cur_lng = lng;
        }); 
        eventBus.$on('close_com', () => {   
            this.showDrawer = false;
        }); 
    },
    methods: {
        getTelLink(tel) {
            return "tel:" + tel;
        },
        toggleDrawer(community) {
            this.showText = false;
            this.selectedCommunity = community;
            this.showDrawer = !this.showDrawer;
        },
        chooseMap(title) {
            this.showDrawer = false;
            this.showchooseMap = !this.showchooseMap;
            this.Communityshow = title;
            

            for (let i = 0; i < drawerContent.length; i++) {
                if (drawerContent[i].title === title) {
                    //给经纬度参数赋值
                    this.lng = this.drawerContent[i].lng;
                    this.lat = this.drawerContent[i].lat;
                }
            }   
        },
        closeGB(){
            this.showchooseMap = false;
        },
        //坐标转换（百度的BD09转高德）
        bd_convert_gd(lng,lat) {
            var bd_lng = lng;
            var bd_lat = lat;
            var pi = 3.14159265358979324 * 3000.0 / 180.0;
            var x = bd_lng - 0.0065;
            var y = bd_lat - 0.006;
            var z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * pi);
            var theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * pi);
            var gd_lng = z * Math.cos(theta);
            var gd_lat = z * Math.sin(theta);
            return [gd_lng,gd_lat];
        },
        redirectToGaodeMap(){
            // 构建跳转 URL
            //var url = "uri.amap.com/navigation?from="+ this.myPositon +",startpoint&to=" + this.lng + "," + this.lat + ",endpoint&mode=car&policy=1&src=mypage&coordinate=gaode&callnative=0"; 
            var [GD_lng, GD_lat] = this.bd_convert_gd(this.cur_lng, this.cur_lat);
            var [GD_lng3, GD_lat3] = this.bd_convert_gd(this.lng, this.lat);
            var url = "http://uri.amap.com/navigation?from="+ GD_lng +","+ GD_lat +
            ",当前位置&to="+ GD_lng3 +","+ GD_lat3 +
            "," + this.Communityshow + "&mode=car&policy=1&src=mypage&coordinate=gaode&callnative=0";
            // 使用 JavaScript 跳转到 URL
            window.location.href = url;
        },
        redirectToBaiduMap(){
            // 构建跳转 URL
            var url = "http://api.map.baidu.com/direction?origin=latlng:" + this.cur_lat +","+this.cur_lng 
            + "|name:当前位置&destination=latlng:"+ this.lat + "," + this.lng 
            + "|name:"+ this.Communityshow +"&mode=driving&region=荆门&output=html&src=webapp.baidu.openAPIdemo' style='color:blue;";
            // // 使用 JavaScript 跳转到 URL
            window.location.href = url;
        }
    },
  }
  </script>
  
