<template>
    <div id="routerP">
        <div id="router-div">
             <img  src="../assets/zhengche.jpg" id="routerP-img01">
             <img  src="../assets/baozhuang1.jpg" id="routerP-img02">
             <img  src="../assets/zonghe.jpg" id="routerP-img03">
        </div>   
    </div>
</template>

<script>
 
export default {
   
    mounted() {
        
        const imgId = this.$route.query.id; 
  
        if(imgId==="0"){
            document.getElementById('routerP-img01').style.display = 'block';
            document.getElementById('routerP-img02').style.display = 'none';
            document.getElementById('routerP-img03').style.display = 'none';
             
        }
        if(imgId==="1"){
            document.getElementById('routerP-img01').style.display = 'none';
            document.getElementById('routerP-img02').style.display = 'block';
            document.getElementById('routerP-img03').style.display = 'none';
             
        }
        if(imgId==="2"){
            document.getElementById('routerP-img01').style.display = 'none';
            document.getElementById('routerP-img02').style.display = 'none';
            document.getElementById('routerP-img03').style.display = 'block';
           
        }

    },
  

}
</script>
<style>
html, body, div {
  margin: 0;
  padding: 0;
}
#routerP{
    width: 100%; 
    height: 100%;
   
}
#router-div{
    height: calc(100vh - 1px);
    overflow:scroll;

}
 
img{
    width: 100%;
    height: auto;
    display: block;
}
</style>